import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { SitesPage } from '../sites/sites';
import { SiteDetailPage } from '../site-detail/site-detail';

@Component({
  selector: 'page-districts',
  templateUrl: 'districts.html'
})
export class DistrictsPage {

  constructor(public navCtrl: NavController) {
  }
  goToSites(params){
    if (!params) params = {};
    this.navCtrl.push(SitesPage);
  }goToSiteDetail(params){
    if (!params) params = {};
    this.navCtrl.push(SiteDetailPage);
  }
}



