import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { DistrictsPage } from '../districts/districts';
import { SitesPage } from '../sites/sites';
import { SiteDetailPage } from '../site-detail/site-detail';

@Component({
  selector: 'page-island-map',
  templateUrl: 'island-map.html'
})
export class IslandMapPage {

  constructor(public navCtrl: NavController) {
  }
  goToDistricts(params){
    if (!params) params = {};
    this.navCtrl.push(DistrictsPage);
  }goToSites(params){
    if (!params) params = {};
    this.navCtrl.push(SitesPage);
  }goToSiteDetail(params){
    if (!params) params = {};
    this.navCtrl.push(SiteDetailPage);
  }
}



