import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { IslandMapPage } from '../island-map/island-map';
import { DistrictsPage } from '../districts/districts';
import { SitesPage } from '../sites/sites';
import { SiteDetailPage } from '../site-detail/site-detail';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController) {
  }
  goToIslandMap(params){
    if (!params) params = {};
    this.navCtrl.push(IslandMapPage);
  }goToDistricts(params){
    if (!params) params = {};
    this.navCtrl.push(DistrictsPage);
  }goToSites(params){
    if (!params) params = {};
    this.navCtrl.push(SitesPage);
  }goToSiteDetail(params){
    if (!params) params = {};
    this.navCtrl.push(SiteDetailPage);
  }
}



